import React from 'react';
import './chatverlauf.css'
import { ClassicSpinner } from "react-spinners-kit";
import ImgView from './../helpers/dialogViewImg'
import { FaCheck } from "react-icons/fa";
import emojiRegex from './emoji/emojiRegex'
import ImageLoader from 'react-load-image';
import api from './../api'
import Spinner from './../helpers/spinner'



function Preloader() {
    return <div className='heicSpinnerChat'><ClassicSpinner size={50} margin={2} color='#000000' loading={true}/></div>;
}



function ListItem(props) {
    return (<div>
                <div className= "chatValue">{props.value}</div>
            </div>);
}



export class Chatbox extends React.Component{
    constructor(props){
        super(props);
        this.state={
            start:false,
            input:'',
            date: new Date(),
            imgViewOpen:false,
            imgView:null,
            imgLoading: 0,
            chatsLength: this.props.chatroom.chat.length,
            imgCount: this.props.chatroom.chat.filter(e => e.imgLow!==null),
            loadingOldMess: false,
            oldMessage: []

        }
        this.backhandle = this.backhandle.bind(this)    
                   
    }


    
    componentDidUpdate(prevProps, prevState){  
        if(this.props.chatroom.chat.length!==prevState.chatsLength){
            console.log('neue nachtricht')
            setTimeout(()=>this.scrollToBottom('auto'), 100)
        }
    }
    

    componentDidMount() {
        //setTimeout(()=>this.scrollToBottom('auto'), 100)
        
  
    }

    handleScroll = (event) =>{
        var node = event.target;
        const bottom = node.scrollHeight - node.scrollTop === node.clientHeight;
        if (bottom) {
            console.log('am boden');
        }
        if(node.scrollTop < 100 && !this.props.chatroom.archiv){
            console.log('oben');
            this.props.chatroom.archiv = true
            this.setState({loadingOldMess: true}, ()=>this.getnextMess())
        }
    }
    
   
    getnextMess = async()=>{
        const name = this.props.chatroom.name
        await api.getNextMessByChatroomName(name).then(res=>{
            if(res.data.next_mess){
                const messlist = res.data.next_mess
                const re_messlist = messlist
                this.setState({loadingOldMess: false, oldMessage: re_messlist})
                /** 
                re_messlist.forEach(e=> this.props.chatroom.chat.unshift(e))
                this.setState({loadingOldMess: false})
                */
            }
        })
        return
        
    }
    // Dialog schlieen ===>
    backhandle = (e) => {
            this.props.closeMess(e)
    }


    scrollToBottom = (e) => {
        this.messagesEnd.scrollIntoView({ behavior: e, block: 'nearest', inline: 'end' });
        
        
    }
    imgViewClose = (e)=>{
        if(e){
            this.setState({imgViewOpen:false})
        }

    }

    
     

    imgChatBlase = (i,user)=>{
        if(i.imgId && i.imgLow){
            return(
                <ImageLoader
                src={i.imgLow}
                >
                    <img className={user? 'imgBlase' : 'imgBlaseChatUser'}  onClick={()=>this.setState({imgViewOpen: true, imgView:i.imgLow})}   alt='...' />
                    <div>Error!</div>
                    <Preloader/>
                </ImageLoader>
                
            )
        }
            
    }

    singleEmoji = (message)=>{
        if (message.length===2 && message.match(emojiRegex().join('|'))) {
            return true;
        } else {
            return false;
        }
    }

    checkLink=(mess)=>{
        if(mess.startsWith('https://')||mess.startsWith('http://') ){
            return(
                <div>
                    <a href={mess} target="_blank">{mess}</a>
                </div>
            )
        }else{
            return mess
        }
    }

    peruseMessage = (chat)=>{
        const dateformat = { 
            //month: 'short', 
            //day: 'numeric',
            hour12: false,
            hour:  "2-digit",
            minute: "2-digit"
        };
        const dateformat2 = { 
            month: 'short', 
            day: 'numeric',
            //hour12: false,
            //hour:  "2-digit",
            //minute: "2-digit"
        };
        if(chat===[]){
            return null
        }
        let user = true
        const lines = [];
        let dateLine = ''
        for(let i of chat){
            if(i.message || i.imgLow){
                if(i.author===this.props.user.id){
                    user = false
                }else{
                    user = true
                }
                let read = 2
                if(i.read){
                    read = 3
                }
                if(new Date(i.date).toLocaleString('de-DE', dateformat2)!==dateLine){
                    dateLine = new Date(i.date).toLocaleString('de-DE', dateformat2)
                    lines.push(
                        <div className='chatDateLine' style={this.props.user.color.backgroundColor==='rgb(93, 100, 102)'? null:{color:'black', borderBottomColor: 'black'}}>{dateLine}</div>
                    )
                }
                lines.push(
                    <div className={user? 'chatinList' : 'chatinListChatUser'}>
                        <div className={user? 'chatblase' : 'chatblaseChatUser'} style={user?{background:this.props.user.color.bubbleColor2}:{background:this.props.user.color.bubbleColor}}>
                            <div className='chatMessage'>
                                {i.imgId?<div>{this.imgChatBlase(i,user)}</div>:null}
                                <div className={user? 'chatMessageBlase' : 'chatMessageBlaseChatUser'} style={user?{color:this.props.user.color.fontColor2}:{color:this.props.user.color.fontColor}} >
                                {this.singleEmoji(i.message)?<div className='singleEmoji'>{i.message}</div>:this.checkLink(i.message)}</div>
                                <div className={user? 'chatdate' : 'chatdateChatUser'} >
                                    <div>{new Date(i.date).toLocaleTimeString('de-DE', dateformat)}</div>
                                    <div>{user? null: this.messageStatus(read)}</div>
                                </div>
                            </div>
                        </div>
                   </div>
                )
            }
        }
        return lines
    }
    
      
    chatVerlauf = ()=> {
        const lines = this.peruseMessage(this.props.chatroom.chat)
        lines.unshift( <div className='SpinnerDivChatVerlauf'><Spinner loading={this.state.loadingOldMess} size={35}/></div>)
        lines.unshift(this.peruseMessage(this.state.oldMessage))
        const listItems = lines.map((message) =>
        <ListItem value={message} key={lines.indexOf(message) } />);
        return listItems; 
    }



    messageStatus=(e)=>{
        switch(e){
            case 1:
                return(
                    <div className='chatMessageStatus'><FaCheck size={8}/></div>
                )
            case 2:
                return(
                    <div className='chatMessageStatus'><FaCheck size={8}/><FaCheck size={8}/></div>
                )
            case 3:
                return(
                    <div className='chatMessageStatusColor'><FaCheck size={8}/><FaCheck size={8}/></div>
                )
            default:
                break
        }
    }
        

    render(){
        setTimeout(()=>this.scrollToBottom('auto'), 100)
        const paneDidMount = (node) => {
            if (node) {
                node.addEventListener('scroll', this.handleScroll);
            }
        };
        return(
            <div className='chatFrame' style={{background:this.props.user.color.backgroundColor}}>
               
                <div className='chatListVerlauf' ref={paneDidMount}>
                    {this.chatVerlauf()}
                    {
                    !this.props.imgSend?
                    null:
                    <div className='heicSpinnerStartDiv'>
                        <ClassicSpinner size={50} margin={2} color='#639cdd' loading={true}/>
                    </div>
                    
                    }
                    <div style={{ float:"left", clear: "both" }}
                    ref={(el) => { this.messagesEnd = el; }}>
                    </div>
                </div>
                <ImgView open={this.state.imgViewOpen} imgUrl={this.state.imgView} close={this.imgViewClose}/>
            </div>     
        )    
        
        
        
    }
        
    
}

export default Chatbox;



  