import React from 'react';
import './index.css';
import ImgView from './../dialogViewImg'




export class Avatar extends React.Component{
    constructor(props){
        super(props);
        this.state={
            name: this.props.name,
            imgLow: this.props.imgLow,
            imgHigh: this.props.imgHigh,

            
            imgViewOpen:false,
            imgView:null,
        }
    }

    

    
    componentDidUpdate(prevProps){
        if(this.props.name!==prevProps.name){
            this.setState({user:this.props.name})
        }
        if(this.props.imgLow!==prevProps.imgLow){
            this.setState({imgLow:this.props.imgLow, imgHigh: this.props.imgHigh})
        }
    
    }

    imgViewClose = (e)=>{
        if(e){
            this.setState({imgViewOpen:false})
        }

    }

    
    
    stringToColor = (str)=> {
        let hash = 0
        for (var i = 0; i < str.length; i++) {
            hash = str.charCodeAt(i) + ((hash << 5) - hash)
          }
          var colour = '#';
          for (var j = 0; j < 3; j++) {
            var value = (hash >> (j * 8)) & 0xFF;
            colour += ('00' + value.toString(16)).substr(-2)
          }
          return colour;
    }
    

    

    render(){
        const divStyle = {
            background: this.stringToColor(this.state.name)
        }
        const ava = this.state.name.charAt(0)+this.state.name.charAt(1)
        const url = this.props.imgLow
        let lastPart = 1
        if(url){
            lastPart = url.substr(url.lastIndexOf('/') + 1);
        }
        if(!this.props.imgLow || this.props.imgLow===undefined || lastPart==='null' || lastPart==='undefined'){
            return(
                <div className='avatar' style ={divStyle}>{ava}</div>
            )
        }else{
            return(
                <div>
                    <img className='avatar'  src={this.state.imgLow} alt={this.state.name} onClick={()=>this.setState({imgViewOpen: true, imgView:this.state.imgHigh})}/>
                    <ImgView open={this.state.imgViewOpen} imgUrl={this.state.imgView} close={this.imgViewClose}/>
                </div>
            )
        }
        
    }
}

export default Avatar;



  