
import React from 'react'
import { FiSmile } from "react-icons/fi";
import Picker from 'emoji-picker-react';
import './index.css'
import Dialog from '@material-ui/core/Dialog';



export class Emoji extends React.Component{
    constructor(props){
        super(props);
        this.state={
            chosenEmoji:null,
            setChosenEmoji:null,
            show: false
        }
    }

    close = ()=>{
        this.setState({show:false})
    }
    onEmojiClick = (event, emojiObject)=>{
        //console.log(emojiObject)
        //console.log(event)
        //this.setState({chosenEmoji:emojiObject.emoji})
        this.props.emoji(emojiObject.emoji)
    }

    emojiPicker=()=>{
        return(
            <Dialog
            className='emojiDialog'
            open={this.state.show===true}
            onBackdropClick= {()=>this.close()}
        
        
            >
            <div className='emojiDialogDiv'>
                <Picker onEmojiClick={this.onEmojiClick} />
            </div>
            </Dialog>
        )
    }
    


    

    

    render(){
        return(
            <div>
            <button className='emojiBtn'
            onClick={()=>this.setState({show:true})}>
                <FiSmile  size={20}/>
            </button>
            {this.emojiPicker()}
            </div>
        )
    }  
    
}

export default Emoji


    

    
      
        
    
