
import path from './../../switch'
//const API_PATH = 'ws://localhost:3000/ws/chat/';
const API_PATH = path.setPATH

class WebSocketService {
  static instance = null;
  callbacks = {};

  static getInstance() {
    if (!WebSocketService.instance) {
      WebSocketService.instance = new WebSocketService();
    }
    return WebSocketService.instance;
  }

  constructor() {
    this.socketRef = null;
  }


  connect(room) {
    const path = API_PATH+room+'/';
    this.socketRef = new WebSocket(path);
    this.socketRef.onopen = (e) => {
      //console.log(e)
      console.log('WebSocket open');
    };
    this.socketRef.onmessage = e => {
      this.socketNewMessage(e.data);
    };

    this.socketRef.onerror = e => {
      console.log(e.message);
    };
    this.socketRef.onclose = () => {
      console.log("WebSocket closed let's reopen");
      //this.connect();
    };
  }
  disconnect(){
    console.log('websocket schllesen')
    this.socketRef.close()
  }

  socketNewMessage(data) {
    const parsedData = JSON.parse(data);
    const command = parsedData.command;
    if (Object.keys(this.callbacks).length === 0) {
      return;
    }
    if (Object.keys(this.callbacks).length > 0) {
    }
    if (command === 'messages') {
      //console.log(command, 'socket')
      this.callbacks[command](parsedData.messages);
    }
    if (command === 'new_message') {
      //console.log('socket_new_mess ')
      this.callbacks[command](parsedData.message);
    }
    if (command === 'new_room') {
      //console.log('socket_new_room')
      this.callbacks[command](parsedData.message);
    }
    if (command === 'create_room') {
      //console.log('socket_create_room')
      this.callbacks[command](parsedData.message);
    }
    if (command === 'read_message') {
      //console.log('socket_read_message')
      this.callbacks[command](parsedData.message);
    }
    if (command === 'init_chat') {
      //console.log('socket_status => user init')
      this.callbacks[command](parsedData.message);
    }
    if (command === 'user_status') {
      //console.log('socket_status => user status')
      this.callbacks[command](parsedData.message);
    }
    if (command === 'hacking') {
      //console.log('socket_status => keyboard hack')
      this.callbacks[command](parsedData.message);
    }
  }

  initChatUser(user) {
    //console.log('initChatUser  => : ', user)
    this.sendMessage({ command: 'init_chat', user });
  }

  fetchMessages(username) {
    //console.log('fetchMessage  => : ', username)
    this.sendMessage({ command: 'fetch_messages', username: username });
  }

  newChatMessage(message) {
    message.command = 'new_message'
    //console.log('newChatMessage  => : ', message, '=> send !')
    this.sendMessage(message); 
     
  }


  onEnterRoom(message) {
    message.command = 'read_message'
    //console.log('on Enter Chatroom  => : ', message, '=> send !')
    this.sendMessage(message); 
     
  }

  contactPing(message) {
    message.command = 'contact_ping'
    //console.log('newPingMessage  => : ', message, '=> send !')
    this.sendMessage(message); 
     
  }

  answerPing(message) {
    message.command = 'answer_ping'
    //console.log('newPingMessage  => : ', message, '=> send !')
    this.sendMessage(message); 
  }

  statusUser(message) {
    message.command = 'user_status'
    //console.log('online  /  offline status  => : ', message, '=> send !')
    this.sendMessage(message); 
     
  }

  hacking(message) {
    message.command = 'hacking'
    //console.log('keyboard hack => : ', message, '=> send !')
    this.sendMessage(message); 
     
  }

  addCallbacks(newMessageCallback) {
    this.callbacks['new_message'] = newMessageCallback;
    this.callbacks['new_room'] = newMessageCallback;
    this.callbacks['create_room'] = newMessageCallback;
    this.callbacks['read_message'] = newMessageCallback;
    this.callbacks['init_chat'] = newMessageCallback;
    this.callbacks['user_status'] = newMessageCallback;
    this.callbacks['hacking'] = newMessageCallback;
  }
  
  sendMessage(data) {
    try {
      this.socketRef.send(JSON.stringify({ ...data }));
    }
    catch(err) {
      console.log(err.message);
    }  
  }

  state() {
    return this.socketRef.readyState;
  }

   waitForSocketConnection(callback){
    const socket = this.socketRef;
    const recursion = this.waitForSocketConnection;
    setTimeout(
      function () {
        if (socket.readyState === 1) {
          console.log("Connection is made")
          if(callback != null){
            callback();
          }
          return;

        } else {
          console.log("wait for connection...")
          recursion(callback);
        }
      }, 1); // wait 5 milisecond for the connection...
  }

}

const WebSocketInstance = WebSocketService.getInstance();

export default WebSocketInstance;
