import React from 'react';
import './index.css';
import { Popover  } from '@material-ui/core';
import Avatar from './../../helpers/avatar'
import { FaTrashAlt } from "react-icons/fa";
import DialogCheck from './../../helpers/dialogCheck'
import ctrl from './../../api_controller'



export class MenuTab extends React.Component{
    constructor(props){
        super(props);
        this.state={
            open: this.props.open,
            imgUrl: this.props.imgHigh,
            user: this.props.user,
            useContact: this.props.useContact,
            dialogCheck: false,
            text:'Achtung !!!, der Chatroom wird mit allen Daten gelscht',
            userDelete: null
        }
      
    }


    componentDidUpdate(prevProps){
        if(this.props.open!==prevProps.open){
            this.setState({open:this.props.open})
        }
        if(this.props.user!==this.state.user){
            this.setState({user:this.props.user})
        }
    }

    componentWillUnmount(){
        this.setState({text:'', userDelete: null})
    }

    close = ()=>{
        this.props.close(true)
    }
    chatroomClose = (deleteId)=>{
        this.props.chatroomClose(deleteId)
        this.props.close(true)
    }

    openSelect = (e)=>{
        this.props.openSelect(e)
    }

    logoutHandle = ()=>{
        this.close()
        this.props.logout(true)
    }

    DialogCheckText = (n, id)=>{
        const text = <div >Achtung !   der Chatroom mit <div style={{fontWeight: "bold"}}>'{n}'</div> und der gesamte Inhalt werden gelscht !</div>
        this.setState({text: text, dialogCheck: true, userDelete: id })
    }


    deleteUserBtn = ()=>{
        const cUserId = this.props.useContact 
        const cMap = this.props.chatroomMap
        const allMap = new Map([...cMap])
        if(cUserId===null || allMap.size===0){
            return null
        }
        const cName = allMap.get(cUserId).userName
        const cPic = allMap.get(cUserId).imgLow
        return(
            <button className='Menubtnchatroomuser' onClick={()=>this.DialogCheckText(cName, cUserId)}>
                <div className='Menuchatroomuser'>
                    <div className='MenuchatroomuserDiv'>
                        <div className='MenuTrashIconDiv'> 
                            <FaTrashAlt className='trashMenuIcon' size={20}/>
                        </div>
                        <div className="MenuchatroomImgDiv">
                            <Avatar name={cName} imgUrl={cPic}/>
                        </div>
                        <div className='MenuchatroomProfilName'>{cName}</div>
                    </div>
                </div>
            </button>
        )
    }
    
    callbackDialogcheck = async(e)=>{
        if(e){
            console.log('huhu')
            const userId = this.state.user.id
            this.setState({dialogCheck: false,})
            const cMap = this.props.chatroomMap
            const allMap = new Map([...cMap])
            const cUserId = this.props.useContact 
            const cName = allMap.get(cUserId).name
            const payload = {
                chatroomName: cName
            }
            console.log(cName)
            const userUpdate = await ctrl.deleteChatroomMapByName(userId, payload)
            if(userUpdate){
                console.log(userUpdate)
                return
                this.setState({dialogCheck: false},()=>this.props.updateUserCallback(userUpdate))
                this.chatroomClose(cName)
                
            }
        }else{
            this.setState({dialogCheck: false,})
        }
       
        
    }

    clearStarage=()=>{
        localStorage.removeItem('login_archa')
        this.logoutHandle()
    }

    



    

    render(){
        /* openSelect....... 

            => 5 === openProfile
            => 6 === openSudoku


        */
        return(
            <Popover 
            id='menu-topbar'
            anchorEl={this.props.anchorEl}
            getContentAnchorEl={null}
            anchorOrigin={{ vertical: "top", horizontal: "right" }}
            transformOrigin={{ vertical: "top", horizontal: "right" }}
            onBackdropClick= {()=>this.close()}
            open={this.state.open===true}
             >
                <div className={!this.props.mobil?'topbarDropdownFrame':'topbarDropdownFrameMobil'}>
                    <button className='topbarDropdownBtn' onClick={()=>this.openSelect(2)} >Profil</button>
                    <button className='topbarDropdownBtn' onClick={()=>this.openSelect(3)} >Farben</button>
                    <button className='topbarDropdownBtn' >ber Archa</button>
                    <button className='topbarDropdownBtn' >Impressum</button>
                    {!this.props.useContact?<button className='topbarDropdownBtn' >lschen</button>:this.deleteUserBtn()}
                    <button className='topbarDropdownBtn' onClick={()=>this.clearStarage()}>Abmelden </button>
                    <button className='topbarDropdownBtn' onClick={()=>this.logoutHandle()}>Ausloggen</button>
                    <DialogCheck text={this.state.text} open={this.state.dialogCheck} checked={this.callbackDialogcheck}></DialogCheck>
                </div>
            </Popover>
        ); 
    }
}

export default MenuTab



  