import React from 'react';
import './index.css';
import { GoX } from "react-icons/go";
import DialogCheck from './../helpers/dialogCheck'
import ImgSize from './../helpers/imgClass'
//import DialogFail from './../dialogFail'






export class Profile extends React.Component{
    constructor(props){
        super(props);
        this.state={
            user: this.props.user,
            userName: this.props.user.userName,
            email: this.props.user.email,
            phone: this.props.user.phone,
            password: this.props.user.password, 
            dialogCheck: false,
            img: null,
            imgUrl: this.props.user.imgHigh,
            newImg: false, 
            change: false,
            colorChange: false,
            side: 1
        }
    }

    handleChange = ({ target }) => {
        this.setState({ [target.name]: target.value , change:true })
    }


    componentDidMount() {
    }
    
    componentWillUnmount() {
    }
    
    close = ()=>{
        this.props.openChat()
    }




    dialogCheckText = ()=>{
        const text = <div >Bist sicher das du die nderungen bernehmen willst <div style={{fontWeight: "bold"}}>{this.state.user.userName}</div> ? </div>
        this.setState({text: text, dialogCheck: true })
        
    }
    callbackDialogcheck = (e)=>{
        this.setState({dialogCheck: false,})
        if(e){
            this.userUpdate()
        }
       
        
    }
    

    croppedImg = async (e)=>{
        const picName = this.state.userName
        const formData = new FormData();
        
        let file = await fetch(e)
        .then(r => r.blob())
        .then(blobFile => new File([blobFile], picName, {type: "image/png" }))  
        /*
        * mobil test

        let photo = {
            uri: e,
            type: 'image/*',
            name: 'file.jpg'

        };
        */
        formData.append('img', file);
        formData.append('thumbnail', file);


        
        
        this.setState({
            img: file, // file by ReacT
            imgUrl:e,
            newImg: true, 
        })
    }

    userUpdate = ()=>{
        if(this.state.newImg){
            const {password, phone, userName, email, img} = this.state
            this.props.userUpgrade({password, phone, userName, email, img})
        }else{
            const {password, phone, userName, email} = this.state
            this.props.userUpgrade({password, phone, userName, email})
        }
        this.close()
    }









    profilImg(){
        if(!this.state.imgUrl){
            return(
                <div className="regisImgDiv">
                    <ImgSize img={this.croppedImg} mobil={this.props.mobil} />
                </div>
            )
        }
        return(
            <div className="regisImgDiv">
                <img className='regisImg' src={this.state.imgUrl} alt={this.state.userName}></img>
                <button className='regisImgBtn' onClick={()=>this.setState({imgUrl: null})}>x</button>
            </div>
        );                    
    }

    saveBtn = ()=>{
        const {change, newImg} = this.state
        if(change || newImg){
            return(
                <button className='profileSaveBtn' onClick={()=>this.dialogCheckText()}>nderung speichern</button>
            )   
        }else{
            return(
                <button className='profileSaveBtn'><div/></button>
            )
        }
    }

    profileMain = ()=>{
        return(
            <div>
                <div className='profileHead'>
                    <button className='profileBackBackBtn' ></button>
                    <button className='profileBackBtn' onClick={()=>this.close()}><GoX size={16}/></button>
                </div>
                <div className='profileMain'>
                    <div className='profileImgDiv'>
                        {this.profilImg()}
                    </div>
                      
                    <div className='profileDataDiv'>
                        <div className='profileBez'>Benutzername:</div>
                        <input className='profileInput' value={this.state.userName} name='userName'  onChange={this.handleChange}></input>
                    </div>
                    <div className='profileDataDiv'>
                        <div className='profileBez'>Email:</div>
                        <input className='profileInput' value={this.state.email} name='email'  onChange={this.handleChange}></input>
                    </div>
                    <div className='profileDataDiv'>
                        <div className='profileBez'>Passwort:</div>
                        <input className='profileInput' value={this.state.password} name='password' type='password' onChange={this.handleChange}></input>
                    </div>
                    <div className='profileDataDiv'>
                        <div className='profileBez'>Telefon:</div>
                        <input className='profileInput' value={this.state.phone} name='phone' type='number' onChange={this.handleChange}></input>
                    </div>
                    <div className='profileDataDiv'>
                        <div className='profileSaveBtnDiv'>
                            {this.saveBtn()}
                        </div>
                            
                        
                    </div>
                </div>

                <DialogCheck text={this.state.text} open={this.state.dialogCheck} checked={this.callbackDialogcheck}></DialogCheck>
            </div>
        ) 
       
    
    }

    

    render(){
        if(this.props.main===2){
            return (
                <div className='profileScreen'>{this.profileMain()}</div>
            )
        }else{
            return null
        }
    }
        
}

export default Profile;



  