import React from 'react';
import './index.css';
import Regis from './register'
import Topbar from './topbar'
import { ClassicSpinner } from "react-spinners-kit";
import Chat from './chat'
import Profile from './profile'
import ColorMenu from './colorMenu'
import color from './helpers/colors'
import ctrl from './api_controller'



export class Home extends React.Component{
    constructor(props){
        super(props);
        this.state={
            mobil: false,
            use: 0,
            email:'',
            password:'',
            user: null,
            newUser: null,
            useContact: null,
            chatroomClose: null,
            main: 1,
            // standart Farbeinstellung
            accentColor: color.accentColor,
            color: {
                backgroundColor: color.white,
                bubbleColor: color.highBlue,
                fontColor: color.white,
                fontColor2: color.grey,
                bubbleColor2: color.black
            },
            chatroomMap: new Map(),
            logout:false         
        }
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this)
        this.frameToLogin = this.frameToLogin.bind(this)
        this.topbar = this.topbar.bind(this)
        this.use = this.use.bind(this)
        this.handleChange = this.handleChange.bind(this)
    }

    handleChange = ({ target }) => {
        this.setState({ [target.name]: target.value })
    }


    


    componentDidMount() {
        window.addEventListener("resize", this.updateWindowDimensions());
        const storage = localStorage.getItem('login_archa')
        if(storage){
            this.storageLogin(JSON.parse(storage))
        }else{
            this.setState({use:1})
        }
        
    }

    updateWindowDimensions() {
        const mobil = window.innerWidth<=600
        document.documentElement.style.setProperty('--vh', `${window.innerHeight/100}px`);
        this.setState({ mobil: mobil});
    }
    
    componentWillUnmount() {
        window.removeEventListener("resize", this.updateWindowDimensions)
    }
    
    
    backHandleRegis=(e)=>{
        if(e){
            this.setState({use: 1})
        }
    }

    openChat = (e)=>{
        this.setState({useContact: e})

    }
    
    openSelect = (e)=>{
        this.setState({main:e})
    }
    use4 = ()=>{
        this.setState({main:1})
    }


    profileUserUprade = (update)=>{
        if(update.img){
            this.updateImg_userDB(update)
        }else{
            this.update_userDb(update)
        }
        
    }

    updateImg_userDB = async(update)=>{
        const img = update.img
        console.log('sende img')
            const storageImg = await ctrl.imgSave(img)
            if(storageImg){
                update.imgLow = storageImg.imgLow
                update.imgHigh = storageImg.imgHigh
                update.imgId = storageImg.id
                this.update_userDb(update)
                await ctrl.deleteImg(this.state.user.imgId)
            }
    }
    
    update_userDb = async(update)=>{
        const userId = this.state.user.id
        const user = await ctrl.updateUser(userId, update)
        if(user){
            console.log('########UPDATE#########')
            const {email, password} = user
            const payload = {email, password}
            localStorage.setItem('login_archa', JSON.stringify(payload));
            this.setState({user})
        }
    }






    storageLogin = async(payload)=>{
        let user = await ctrl.getUserbyEAndP(payload)
        if(user){
            //console.log(user, 'api passEmail')
            // nur bis alle profile gendert sind.....
            if(!user.color){
                console.log('neu fgt color daten hinzu')
                const color = this.state.color
                const payload = {color}
                user = await ctrl.updateUser(user.id, payload)
            }
            // lade Map chatrooms from user
            const chatroomMap = await ctrl.getCharoomList(user.id)
            if(chatroomMap){
                this.setState({user, use:4, chatroomMap})
            }  
        }else{
            window.alert('email oder Passwort stimmen nicht')
            //localStorage.removeItem('login_archa')
            this.setState({use:1})
        }
    }

    login = async()=>{
        const email = this.state.email
        const password = this.state.password
        if(email===''&&password===''){
            return
        }
        const payload = {email, password}
        let user= await ctrl.getUserbyEAndP(payload)
        if(user){
            localStorage.setItem('login_archa', JSON.stringify(payload));
            const chatroomMap = await ctrl.getCharoomList(user.id)
            if(chatroomMap){
                this.setState({user, use:4, chatroomMap})
            }
        }else{
            window.alert('email oder Passwort stimmen nicht')
            this.setState({use:1})
        }
    }
    chatroomClose= (e)=>{
        if(e){
            this.setState({chatroomClose:e})
        }
    }

    newUser = (userDb)=>{
        this.setState({user: userDb, use:4})
    }

    addUser = (user)=>{
        if(user._id===this.state.user._id){
            this.loadMap(user)
        }  
    }

    loadMap = async(user)=>{
        const chatroomMap = await ctrl.getCharoomList(user.id)
            if(chatroomMap){
                this.setState({user, use:4, chatroomMap})
            }
    }



    logoutHandle = (e)=>{
        console.log('ausloggen....')
        if(e){
            this.setState({
                use: 1,
                email:'',
                password:'',
                newUser: null,
                user: null,
                chatroomMap: new Map(),
                addEventListener: null
            },()=>this.remoteUser())
        }
    }
    remoteUser = ()=>{
        setTimeout(()=>this.setState({user:null}),300)
    }

    handleKeyDown = (event) => {
        if (event.key === 'Enter') {
            this.login()
        }
      }

    frameToLogin(){
        return(
            <div className='loginframeDiv'  onKeyDown={this.handleKeyDown}>
                <div className='loginframeTxt1'>ARCHA</div>
                <div className='placeholderDiv'></div>
                <div className='loginframeTxt3'>Cloud - messenger</div>
                <div className='placeholderDiv'></div>
                <div className='loginframeTxt2'>Willkommen!</div>
                <div className='loginframeTxt2'>log dich ein oder regestriere dich </div>
                <div className='placeholderDiv'></div>
                <div className='loginInputDiv'><input className='loginInput' type='text' name='email' placeholder='E-mail' value={this.state.email} onChange={this.handleChange}></input></div>
                <div className='placeholderDivHalf'></div>
                <div className='loginInputDiv'><input className='loginInput' type='password' name='password' placeholder='Passwort'value={this.state.password} onChange={this.handleChange}></input></div>
                <button className='loginBtn' onClick={()=>this.setState({use:3},()=>this.login())}>Login</button>
                <button className='regesBtn' onClick={()=>this.setState({use:2})}>registrieren</button>
            </div>
        )
    }
    topbar(){
        return(
            <div className='topbarDivHome'>
                <Topbar 
                    user={this.state.user} 
                    updateUserHome={this.addUser} 
                    mobil={this.state.mobil} 
                    logout={this.logoutHandle} 
                    useContact={this.state.useContact}
                    chatroomClose={this.chatroomClose}
                    chatroomMap={this.state.chatroomMap}
                    openSelect={this.openSelect}
                />
            </div>
        )
    }

    use(){
        switch(this.state.use){
            case 0:
                const loginLoad =  this.state.use === 0? true : false
                return(
                    <div className='regisDivSwitchLoad'>
                        <div className='heicSpinner'>
                            <div className='heicSpinnerStartDiv'>
                                <ClassicSpinner size={50} margin={2} color='#639cdd' loading={loginLoad}/>
                            </div>
                        </div> 
                    </div>
                )
            case 1:
                return(
                    <div className='regisDivSwitch'>
                        {this.frameToLogin()}
                    </div>
                )
            // neuer User.....
            case 2:
                return(
                    <div className='regisDivSwitch'>
                        <Regis mobil={this.state.mobil} back={this.backHandleRegis} newUser={this.newUser}/>
                    </div>
                )
            // einloggen.....
            case 3:
                const loading = true
                return(
                    <div className='regisDivSwitch'>
                        <div className='loginframeDiv'>
                            <div className='placeholderDiv'></div>
                            <div className='loginframeTxt3'>lade... </div>
                            <div className='heicSpinner'><ClassicSpinner size={50} margin={2} color='#639cdd' loading={loading}/></div> 
                        </div>               
                    </div>
                )
            // Main Paiper....
            case 4:
                return(
                    <div className='regisDivSwitch'>
                        <div className='loginframeDiv'>
                            <Chat 
                                user={this.state.user} 
                                useContact={this.openChat} 
                                chatroomClose={this.state.chatroomClose} 
                                mobile={this.state.mobil} 
                                chatroomMap={this.state.chatroomMap}
                                updateMap={this.loadMap}
                                main={this.state.main}
                            />
                            <Profile
                                openChat={this.use4}
                                user={this.state.user}
                                userUpgrade={this.profileUserUprade}
                                main={this.state.main}
                            />
                            <ColorMenu
                                openChat={this.use4}
                                user={this.state.user}
                                color={this.profileUserUprade}
                                main={this.state.main}
                            />
                        </div>               
                    </div>
                )
            default: return null
        }
    }
    

    render(){
        return(
            <div className={!this.state.mobil?'homeScreen':'homeScreenMobil'} >
                {this.topbar()}
                <div className={!this.state.mobil?'loginFrame':'loginFrameMobil'}>
                    {this.use()}
                </div>
            </div>
        ); 
    }
}

export default Home;



  