  // suche user nach id
  export async function getUser(id){
    const e = await api.getUserById(id).then(res=>{
      if(res.data.success){
        const user = res.data.data
        return user
      }else{
        return false
      }
      
    })
    return e
  }


  // suche user nach Email und password
