  export async function getCharoomList(id){
    const chatroomMap = new Map()
    console.log('lade liste chatroom')
    await api.getChatroomMapByUserId(id).then(res=> {
      const resData = res.data.chatrooms
      console.log(resData)
      const chatrooms = Object.keys(resData)
      console.log(chatrooms)
      chatrooms.map(e=> chatroomMap.set(e, resData[e]))
    })
    return chatroomMap
    
  }


  export async function deleteChatroomMapByName(userId,chatroomName){
    console.log(userId, chatroomName)
    const cMap = await api.deleteChatroomMapByName(userId, chatroomName).then(res=>{
      console.log('delete chatrrom    ', res.data)
      return res.data
    })
    if(cMap){
      return cMap
    }
    
  }



















  



