
import WebSocketInstance from './WebSocket'


const waitForSocketConnection = (callback)=> {
  const component = this;
  setTimeout(
     () =>  {
      // Check if websocket state is OPEN
      if (WebSocketInstance.state() === 1) {
        console.log("Connection is made")
        callback();
        return;
      } else {
        console.log("wait for connection...")
        component.waitForSocketConnection(callback);
      }
  }, 2500); // wait 100 milisecond for the connection...
}


export default function () {
  console.log('connection ws')
  
  

  function callbackPool(messagesCallback) {
      WebSocketInstance.addCallbacks(messagesCallback)
    
  }

  function connect(roomName){
    WebSocketInstance.connect(roomName)
  }

  function disconnect(){
    WebSocketInstance.disconnect()
  }

  function unregisterHandler() {
  }
  function register(user) {
    waitForSocketConnection(()=>
    
      WebSocketInstance.initChatUser(user)
    )
  }


  function onEnterRoom(chatroomName, user) {
    const messageObject = {
      room: chatroomName,
      user: user
    };
    WebSocketInstance.onEnterRoom(messageObject);
  }

  function message(chatroomName, msg, imgHigh, imgLow, imgId, userId) {
    const messageObject = {
      author: userId,
      text: msg,
      room: chatroomName,
      imgHigh: imgHigh,
      imgLow: imgLow,
      imgId: imgId
    };
    WebSocketInstance.newChatMessage(messageObject);
  }

  function pingContact(contact) {
    const messageObject = contact
    messageObject.text = 'new_room'
    
    WebSocketInstance.contactPing(messageObject);
  }

  function pingAnswer(contact) {
    const messageObject = contact
    messageObject.text = 'create_room'
    
    WebSocketInstance.answerPing(messageObject);
  }

  function userStatus(contact) {
    const messageObject = contact
    messageObject.text = 'user_status'
    
    WebSocketInstance.statusUser(messageObject);
  }

  function hack(chatroomName, charLength, author) {
    const messageObject = {
      text: 'hacking',
      author,
      charLength,
      chatroomName,
    };
    WebSocketInstance.hacking(messageObject);

  }



  return {
    register,
    message,
    hack,
    callbackPool,
    unregisterHandler,
    disconnect,
    connect,
    pingContact,
    pingAnswer,
    onEnterRoom,
    userStatus,
  }
}