import React from 'react';
import './index.css';
import { MdMenu } from "react-icons/md";
import MenuClass from './dialogMenu'
import Avatar from './../helpers/avatar'
import socket from './../socket/socket'
import color from './../helpers/colors'
import { ClassicSpinner } from "react-spinners-kit";





export class Topbar extends React.Component{
    constructor(props){
        super(props);
        this.state={
            user: this.props.user,
            imgUrl: null,
            build:false,
            dialogCheck:false,
            inviIndex: null,
            dropdown: false,
            client: socket(),
            accentColor: color.accentColor,
            s3upload: false,

        }
        this.divRef = React.createRef();
        this.userData = this.userData.bind(this)
    }


    
    componentDidUpdate(prevProps, prevState){
        if(this.props.user!==prevState.user){
            if(this.props.user!==null){
               this.setState({user:this.props.user, build:true})
            }else{
               this.setState({
                   user:this.props.user, 
                   build:false,
                   imgUrl: null,
                   invitUserName:[],
                   invitUserImgUrl:[],
                   dialogCheck:false,
                   inviIndex: null,
                   dropdown: false,
                   useContact: this.props.useContact,
                   anchorEl: null
                })
            }
           
        }
    }

    

    

   
    getImg = async()=>{
        if(this.state.user===null){
            this.setState({build:false})
            return
        }
        if(!this.props.user.imgUrl){
            this.setState({build:true})
        }
        const imgUrl = this.props.user.imgLow
        this.setState({imgUrl:imgUrl})
    }


    

    
    updateUser = (e)=>{
        this.props.updateUserHome(e)
    }


    
    menuClose = (e)=>{
        if(e){
            this.setState({dropdown:false})
        }
    }
    chatroomClose= (e)=>{
        if(e){
            this.props.chatroomClose(e)
        }
    }
    logoutHandle = (e)=>{
        this.setState({build:false})
        this.props.logout(e)
    }
    openSelect = (e)=>{
        this.setState({dropdown:false})
        this.props.openSelect(e)
    }

    

    dialogCallback = (e)=>{
        if(e){
            this.setState({dialogCheck:false},()=>this.addChatUser(this.state.inviIndex))
        }else{
            this.setState({dialogCheck:false})
        }
    }
    handleClickDropdown = ()=> {
        this.setState({ anchorEl: this.divRef.current, dropdown:true });
      };

    

    userData(){
        const user = this.props.user
        if(this.state.build){
            return(
                <div className='topScreenDiv'>
                    <div className='topScreenLeftSide'>
                        <div className='topSreenAppName'>ARCHA</div>
                    </div>
                    <div className='topScreenRightSide'>
                        <div className='topScreenUser' style={{color: color.accentColor}}>{this.state.user.userName}</div>
                        
                        <div className="topScreenImgDiv">
                            <Avatar name={user.userName} imgLow={this.state.user.imgLow} imgHigh={this.state.user.imgHigh}/>
                        </div>
                        <div ref={this.divRef}>
                        <button 
                            className='topScreenMenuBtn' 
                            aria-owns={this.state.anchorEl ? "menu-topbar" : null}
                            aria-haspopup="true"
                            onClick={()=>this.handleClickDropdown()}><MdMenu size={35}/>
                        </button>
                        </div>
                    </div>
                </div>
            )
        
        }else{
            return(
                <div className='topScreenDiv'>
                    <div className='topScreenLeftSide'>
                        <div className='topSreenAppName'>ARCHA</div>
                    </div>
                    <div className='topScreenRightSide'>
                        <div className='topScreenUser'></div>
                        <div className="topScreenImgDiv">
                        </div>
                    </div>
                </div>
            )
        }

   }


    

    render(){
        return(
            <div className='topScreen'>
                {this.userData()}
                <MenuClass 
                    open={this.state.dropdown} 
                    close={this.menuClose} 
                    chatroomClose={this.chatroomClose}
                    useContact={this.props.useContact} 
                    logout={this.logoutHandle} 
                    user={this.props.user} 
                    anchorEl={this.state.anchorEl}
                    updateUserCallback={this.updateUser} 
                    mobil={this.props.mobil}
                    chatroomMap={this.props.chatroomMap}
                    openSelect={this.openSelect}
                    
                />
            </div>
        ); 
    }
}

export default Topbar;



  