
import React from 'react'
import './index.css'
import ChatImg from './chatImage'
import Emoji from './emoji'
import ctrl from './../api_controller'




export class InputDevice extends React.Component{
    constructor(props){
        super(props);
        this.state={
            input: '',
            img: null,
            storageImg: {
                imgLow: null,
                imgHigh: null,
                id: null
            },
            imgId: null,
            client: this.props.socket,
            
        }
        
        
                         
    }

    componentDidMount(){
        
    }

    componentWillUnmount(){
    }


    // input user => eingabefelder setten......
    handleChange = ({ target }) => {
        const croom = this.props.chatroomName
        const charCount = target.value.length
        const author = this.props.user.id
        const sendList = [1,5,10,15,20,25,30,35,40,45,50,55,60,65,70]
        if(sendList.find(e => e===charCount)){
            this.state.client.hack(croom, target.value.length, author )
          }
        
        this.setState({ [target.name]: target.value });
    }  

    // input user => emoji props
    handleChangeEmoji = (e) => {
        this.setState({input:this.state.input+e});
    } 

   
    useImg = async (e,txt)=>{ 
        const picName = this.props.user.userName
        let file = await fetch(e)
        .then(r => r.blob())
        .then(blobFile => new File([blobFile], picName, {type: "image/jpeg" }))
        const storageImg = await ctrl.chatImgSave(file)
        if(storageImg){
            this.setState({
                img:file,
                storageImg,
                input: txt,
            },()=>this.send()) 
        }
    }

    send = ()=>{
        this.props.send(this.state.input, this.state.storageImg.imgLow, this.state.storageImg.imgHigh, this.state.storageImg.id)
        this.setState({
            input:'',
            img: null,
            storageImg: {
                imgLow: null,
                imgHigh: null,
                id: null
            },
            imgId: null,
        })
    }



    

    

    render(){
        return(
            <div className='chatEingabe'>
                <textarea className="chatInput" placeholder='deine Nachricht...' name="input" value={this.state.input}  onChange={this.handleChange} ></textarea>
                <div className='chatEingabeIcons'>
                    <Emoji emoji={this.handleChangeEmoji}/>
                    <ChatImg mobile={this.props.mobile} img={this.useImg}/>
                </div>
                <div className='chatEingbeRight'>
                    <button className="chatBtn" onClick={()=>this.send()}>senden</button>     
                </div>
            </div>
        )
                            
                       
            
            
        
    }
}       
        
    


export default InputDevice;