
import React from 'react'
import socket from './../socket/socket'
import './index.css'
import Chatverlauf from './chatVerlauf'
import Chatroom from './chatroom'
import { GoX, } from "react-icons/go";
import Avatar from './../helpers/avatar'
import InputDevice from './inputDevice'
import getId from './../helpers/get_Id'
import ctrl from './../api_controller'



export class Chatbox extends React.Component{
    constructor(props){
        super(props);
        this.state={
            client: socket(),
            user: this.props.user,
            chatUser: null,
            chatroomMap: this.props.chatroomMap,
            login: true,
            chatroom: {},
            joinedUser: [],
            connect: false,
            chatroomView: true,
            default: null,
            online: [],
            hacking: false,
            imgSend: null,
            
        }
        this.addMessagesCallback = this.addMessagesCallback.bind(this)
        this.onSendMessage = this.onSendMessage.bind(this)  
    }



    componentDidMount(){
        this.connectSocket()
        this.state.client.callbackPool(this.addMessagesCallback)
        this.setName(this.props.user.contacts)
        
        
    }








    connectSocket = ()=>{
        const rooms = Array.from(this.state.chatroomMap.values())
        if(rooms){
            rooms.forEach(e=>{
                console.log(e, 'connect')
                this.state.client.connect(e.name)
            })
        }
        // connect ping__room
        const ping_room = this.state.user.id
        this.state.client.connect(ping_room)
    
        
        
    }


    // erstellt user im Socket........
    setName = (contacts) => {
        const payload = {
            user: this.state.user.id,
            contacts: contacts
        }
        this.state.client.register(payload)
        this.setState({
            connect: true
        })
    }




    componentWillUnmount(){
        console.log('klasse chat geschlossen')
        const onlineUser = this.state.online
        const user = this.state.user.id
        onlineUser.forEach(e=>{
            const answer = {
                chatroomName: e,
                answer: false,
                user: user
            }
            this.state.client.userStatus(answer)
        })
        this.state.client.disconnect()
        this.setState({
            chatroom: null,
            chatUser: null,
            chatroomView: true,
        })
    }

    // schliee -Dialog -message -chatroom
    close = () =>{
        console.log(' close chatroom => State reset')
        this.props.useContact(null)
        this.setState({
            chatroom: null,
            chatUser: null,
            chatroomView: true
        })
    } 



    



    componentDidUpdate(prevProps, prevState){
        if(JSON.stringify(this.props.user)!==JSON.stringify(prevProps.user)){
            this.setState({ user: this.props.user})
        }
        if(this.props.chatroomClose!==prevProps.chatroomClose){
            console.log('chat wird geschlossen')
            this.close()
        }
        if(this.props.chatroomMap.size>prevState.chatroomMap.size){
            console.log('NDERUNG _chatroomMap')
            this.setState({chatroomMap: this.props.chatroomMap})
        }
    }










    


    
 










    














    
    





    runChatroom = (e)=>{
        console.log('FFNE CHAT')
        this.props.useContact(e)
        const cRoom  = this.state.chatroomMap.get(e)
        if(!('chat' in cRoom)){
            cRoom.chat = []
        } 
        // user geht in den Chatroom => socket read message!
        this.state.client.onEnterRoom(cRoom.name, this.state.user.id)    
        this.setState({
            chatUser: cRoom.id,
            chatroom: cRoom,
            chatroomView: false
        })  
    }

    runNewChatroom = (payload ) =>{
        const sendData = {
            id: this.state.user.id,
            imgHigh: this.state.user.imgHigh,
            imgLow: this.state.user.imgLow,
            room: payload.user.id,
            userName: this.state.user.userName,
        }
        this.state.client.pingContact(sendData)

    }

    

    // erstellt die Datenbank fr neuen Chatroom!!!!!!
    createChatroomDb = async(selected)=>{
        const userId = this.state.user.id
        const chatUserId = selected.id
        const name = getId()
        const users = {creator: chatUserId, members: [userId]}
        const payload = {name, users}
        const chatroom = await ctrl.createChatroom(payload)
        //console.log(userId, ' : ', chatUserId)
        //console.log(this.state.chatroomMap)
        if(chatroom){
            const key = chatroom.id
            const sendData = {
                room: selected.id,
                roomId: chatroom.id,
                inquiryUser: this.state.user.id
            }
            this.state.client.pingAnswer(sendData)
            this.props.chatroomMap.delete(chatUserId.toString())
            //console.log(this.state.chatroomMap)
            chatroom.id = chatUserId
            chatroom.userName = selected.userName
            chatroom.imgLow = !selected.imgLow?null:selected.imgLow
            chatroom.imgHigh = !selected.imgHigh?null:selected.imgHigh
            chatroom.chat = []
            this.props.chatroomMap.set(key, chatroom)
            this.state.client.connect(chatroom.name)
            this.setState({
                chatUser: chatUserId,
                chatroom: chatroom,
                chatroomView: false
            }) 
        }

        
    }

    answerContact = async(message)=>{
        const roomMap = await ctrl.getCharoomList(this.state.user.id)
        if(roomMap){
            const newRoom = roomMap.get(message.roomId.toString())
            //console.log('newRoom', newRoom)
            this.state.chatroomMap.set(message.roomId.toString(), newRoom)
            this.state.client.connect(newRoom.name)
            this.setState({default: null})
        }
        
    }
     

    
   
    

    

    

    
            
    
    
    

   

    
    
    
    


    

    
    
    


    
    

    // INPUTEVICE => sende MAessage
    onSendMessage = (m, imgLow, imgHigh, imgId) =>{ 
        const message = m
        const chatroomName = this.state.chatroom.name
        const user_id = this.state.user.id
        if(imgLow===null){
            if(m===''){
                return
            }
            //console.log(message)
            this.state.client.message(chatroomName, message,'','', null, user_id)
        }else{
            
            this.state.client.message(chatroomName, message, imgLow, imgHigh, imgId, user_id)
            this.setState({
                default: null, 
                //imgSend: imgId
            })
            
            /**
             *    MESSAGE
             * 
             * 
             * 
             * author: 24114211353796
                chatroomName: "6026c72221e6095ee95f4e76"
                date: "2021-02-12T18:21:31.627Z"
                id: 24321794072051
                imgHigh: ""
                imgId: null
                imgLow: ""
                message: "hi"
                read: true
             */
        }
    }



    


    ////////////////////////////////////////////////////////////////
    ////////////____socket Callback______//////////////////////////

    addMessagesCallback(message){
        //console.log(message,'....')
        const getIdByValue = (searchValue)=>{
            const map = this.state.chatroomMap
            for (let [key, value] of map.entries()){
              if (value.name === searchValue)
                return key;
            }
        }
        switch(message.text){



            
            case 'new_room':
                window.alert('fick die wand an')
                this.state.chatroomMap.set(message.id, message)
                this.setState({default:null})
            break


            
            case 'read_message':
                if(message.reader){
                    //console.log(message, 'read', this.state.user.id)
                    //console.log(this.state.chatroom)
                    if(this.state.chatroom.name === message.chatroomName){
                        if(message.user !== this.state.user.id ){//
                            this.state.chatroom.chat.forEach(e => {
                                e.read = true
                            
                            })
                            this.setState({chatroom: this.state.chatroom})
                        }else{
                            this.state.chatroom.chat.forEach(e => {
                                if(e.author!==this.state.user.id ){
                                    e.read = true
                                }
                            
                            })
                            this.setState({chatroom: this.state.chatroom})
                        }
                    }
                }
            break



            case 'hacking':
                const hackTime = setTimeout(()=>{this.setState({hacking: false})},1500)
                if(!this.state.chatroomView){
                    const chatroomName = this.state.chatroom.name
                    const userId = this.state.user.id
                    if(message.hack && chatroomName === message.chatroomName && userId !== message.author){
                        if(!this.state.hacking){
                            this.setState({hacking: true}, ()=>hackTime)
                        }
                    }
                }
            break



            case 'init_chat':
                if(message.online){
                    const user_self = this.state.user.id
                    const onlineUser = message.online
                    const answer = {
                        chatroomName: onlineUser,
                        answer: true,
                        user: user_self
                    }
                    this.state.client.userStatus(answer)
                    if(this.state.online.find(e=>e===onlineUser)){
                        return
                    }
                    this.setState({online: this.state.online.concat(onlineUser)})
                } 
            break




            case 'user_status':
                if(message.status){
                    const onlineUser = message.user
                    this.setState({online: this.state.online.concat(onlineUser)})
                    break
    
                }else if(!message.status){
                    const onlineUser = message.user
                    if(this.state.online.find(e=>e===onlineUser)){
                        this.state.online.forEach((e, i)=>{
                            if(e===onlineUser){
                                this.state.online.splice(i, 1)
        
                            }
                        })
                        this.setState({online: this.state.online})
                    }
                    break
                }
            break



            case 'create_room':
                window.alert('fick die wand nochmal an')
                this.answerContact(message)
            break



            case 'new_message':
                const mapId = getIdByValue(message.chatroomName)
                //console.log('addMessage=>    ',message, mapId)
                
                // info message read
                if(this.state.chatroom){
                    const c_name = this.state.chatroom.name
                    const author = message.author
                    const mess_c_name = message.chatroomName
                    if(c_name===mess_c_name && author!==this.state.user.id){
                        console.log(author, 'author  user  ',this.state.user.id)
                        console.log(c_name, 'c_nsme  ', mess_c_name, 'mess_c')
                        /// reading message
                        this.state.client.onEnterRoom(c_name, this.state.user.id)
                    }
                }
                if(mapId){
                    if(this.props.chatroomMap.has(mapId)){

                        const room = this.props.chatroomMap.get(mapId)
                        room.chat.push(message)
                        this.state.chatroomMap.set(mapId, room)
                        if(message.imgHigh=== this.state.imgSend){
                            this.setState({default:null, imgSend: null})
                        }
                        this.setState({default:null})
                    }
                    
                }else{
                    console.log('unbekannte Nachricht')
                }
            break

            default:
                break
        }
    }

    
       




    scrollChatToBottom() {
        this.panel.scrollTo(0, this.panel.scrollHeight)
    }


    

    chatverlauf =()=>{
        const hack = this.state.hacking
        const online = this.state.online.find(e=>e===this.state.chatUser)
        return(
            <div className='ChatverlaufCon'>
                <div className='chathead'>
                    <div className='chatHeadImgDiv'><Avatar name={this.state.chatroom.userName} imgLow={this.state.chatroom.imgLow} imgHigh={this.state.chatroom.imgHigh}/></div>
                    <div className='chatheadName'>{this.state.chatroom.userName}{!hack?null:<div className='chartoomWrite'> schreibt...</div>}</div>
                        {!online?null:<div className='chatroomJoinedHead'>online</div>}
                        <button className='chatheadBtn' onClick={()=>this.close()}><GoX size={16}/></button>
                    </div>
                    <div className='chatContainer'>
                        <Chatverlauf 
                            chatroom={this.state.chatroom} 
                            closeMess={this.close} 
                            user={this.props.user}
                            imgSend= {this.state.imgSend}
                            socket = {this.state.client}
                        />
                        <InputDevice
                            mobile = {this.props.mobile}
                            send = {this.onSendMessage}
                            chatroomName = {this.state.chatroom.name}
                            user = {this.props.user}
                            socket = {this.state.client}
                            
                        />
                    </div>
                </div>
        )
    }

    

    

    render(){
        if(this.state.connect && this.props.main===1){
            return(
                <div className='ChatDiv'>
                    {!this.state.chatroomView?this.chatverlauf():null}
                    <div className='chatroomFrame'>
                        <Chatroom 
                            user={this.state.user} 
                            openChat={this.runChatroom}
                            openNewChat={this.runNewChatroom} 
                            online={this.state.online}
                            chatroomMap={this.state.chatroomMap}
                            chatroomView={this.state.chatroomView}
                            createRoom={this.createChatroomDb}
                        />
                    </div>  
                </div>
            )
            
        }else{
            return null
        }
    }
}       
        
    


export default Chatbox;