import React from 'react'
import './chatroom.css'
import Avatar from './../helpers/avatar'
import socket from './../socket/socket'
import Search from './search'
import DialogCheck from './../helpers/dialogCheck'
import { AiOutlinePicture } from "react-icons/ai"



function ListItem(props) {
    return (<div>
                <div className= "chatValue">{props.value}</div>
            </div>);
}

export class Chatroom extends React.Component{
    constructor(props){
        super(props)
        this.state={
                date: new Date(),
                user: this.props.user,
                chatrooms: this.props.user.chatrooms,
                chatroomMap: this.props.chatroomMap,
                loading: true,
                openChat: null,     
                chatMap: null,
                usersMap: null,
                chatUserJoined: null,
                avatar:null,
                client: socket(),
                start: true,
                chatUser: null,
                openDialogCheck: false,
                progress:false,
        }
    }

    handleChange = ({ target }) => {
        this.setState({ [target.name]: target.value })
    }

    componentDidUpdate(prevProps, prevState){
    }

    componentDidMount(){
        console.log('chat list')
        
    }
    
    componentWillUnmount(){
        //console.log('unmount chatroom liste')
    }
   
    

     




    runopenChatbox = (id) => {
        this.props.openChat(id)
    }



    
    // Callback Dialog-Check
    dialogCallback = (e)=>{
        if(e){
            this.setState({openDialogCheck:false})
        }else{
            this.setState({openDialogCheck:false})
        }
    }

    // callback search user->update
    searchUpdate = (user)=>{
        this.setState({user})
    }





    





    messageReads = (chatroom)=>{
        /** 
        console.log('_______________lade messages_______________')
        console.log(chatroom.chat)
        console.log('___________________________________________')
        */
        const author = chatroom.chat.filter(e=>e.author !== this.props.user.id)
        const count = author.filter(e=>e.read === false).length
        return count
    }

    

    inquiry = (i ,name, pic, picHigh)=> {
        return(
            <div className='chatroominListe'>
                <button className='btnchatroomuser' onClick={()=>this.props.createRoom(i)}>
                    <div className='chatroomuser'>
                        <div className='chatroomuserDiv'>
                            <div className="chatroomImgDiv" >
                                <Avatar name={name} imgLow={pic} imgHigh={picHigh}/>
                            </div>
                            <div className='chatroomSpecsDiv'>
                                <div className='chatroomTxtDivTop'>
                                    <div className='chatroomProfilName'>{name}</div>
                                </div>
                                <div className='chatroomTxtDivBotton'>
                                    <div className='chatroomLastChatDiv' >
                                        ...fragt nach einem gemeinsamen Chatroom
                                    </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </button>
                </div>
        )
    }





    chatrooms = ()=> {
        const dateformat = { 
            month: 'short', 
            day: 'numeric',
            hour12: false,
            hour:  "2-digit",
            minute: "2-digit"
        };
        const sortMap = (e)=>{
            if(e[1].chat){
                const lastchat = e[1].chat[e[1].chat.length - 1]
                if(lastchat){
                    //console.log(lastchat.date, e[1].userName )
                    const dateTime = new Date(lastchat.date)
                    return dateTime
                }
            }
            return new Date(2021, 0, 1)  
        }


            const lines = [];
            // Chatuser mit Chatroom => map
            const chatroomMap = this.props.chatroomMap
            //console.log([...chatroomMap.entries()])
            // CHatuser on Chatroom => map
            const mapArray = [...chatroomMap.entries()]
            const sortArray = mapArray.sort((a, b) => sortMap(b) - sortMap(a))
            const MapSort = new Map(sortArray)
            const keys = MapSort.keys()
            for(let i of keys ){
                const text = chatroomMap.get(i).text
                const online = this.props.online.find(e=>e===chatroomMap.get(i).id)
                const name = chatroomMap.get(i).userName
                const pic = chatroomMap.get(i).imgLow
                const picHigh = chatroomMap.get(i).imgHigh
                if(text==='new_room'){
                    lines.push(this.inquiry(chatroomMap.get(i),name, pic, picHigh))
                }else{
                    const chats = chatroomMap.get(i).chat
                const newMess = this.messageReads(chatroomMap.get(i))
                const lastchat = chats.length<=0?'Leerer Chat':chats[chats.length-1].message
                const lastchatUser = chats.length<=0?null:chats[chats.length-1].author
                const lastchatTime = chats.length<=0?'':chats[chats.length-1].date
                
                    lines.push(
                        <div className='chatroominListe'>
                        <button className='btnchatroomuser' onClick={()=>this.runopenChatbox(i)}>
                            <div className='chatroomuser'>
                                <div className='chatroomuserDiv'>
                                    <div className="chatroomImgDiv" >
                                        <Avatar name={name} imgLow={pic} imgHigh={picHigh}/>
                                        {newMess===0?null:
                                            <div className='underDateDiv'>
                                                <div className='messageCountDiv'>{newMess}</div>
                                            </div>
                                        }
                                    </div>
                                    <div className='chatroomSpecsDiv'>
                                        <div className='chatroomTxtDivTop'>
                                            <div className='chatroomProfilName'>{name}</div>
                                            <div className='chatroomProfilDate'>
                                                {new Date(lastchatTime).toLocaleDateString('de-DE', dateformat)}
                                            </div>
                                        </div>
                                        <div className='chatroomTxtDivBotton'>
                                            <div className='chatroomLastChatDiv' >
                                                {lastchat===''?<AiOutlinePicture size={35}/>:<div>
                                                    <div style={{color: 'rgb(99, 156, 221)'}}>{lastchatUser!==this.props.user.id?null:'Du: '}</div>"{lastchat}"</div>}
                                            </div>
                                            {!online?null:<div className='chatroomJoined'>online</div>}
                                        </div>
                                    </div>
                                </div>
                                
                            </div>
                            </button>
                       </div>
                    )

                }
                
            }
            
            const listItems = lines.map((room) =>
            <ListItem value={room} key={lines.indexOf(room) } />);
            return listItems;
       
        
}


        

    

   
    
    render(){
        if(this.props.chatroomView){
            return(
                <div className='chatroomDiv'>
                    <div className='chatListRooms'>
                        <Search user={this.state.user} selectUser={this.props.openNewChat}/>
                        <div className='chatroomListeFrame'>
                            {this.chatrooms()}
                            <DialogCheck 
                                text={<div>mchtest du <div style={{fontWeight: "bold"}}></div> zu dein kontakten hinzufgen ?</div>}
                                open={this.state.openDialogCheck}
                                checked={this.dialogCallback}
                            />
                        </div>
                    </div>
                </div>  
            )
        }else{
            return null
        }
    } 
        
    
    
}

export default Chatroom   
