import React from 'react';
import './chatroom.css'
import DialogCheck from './../helpers/dialogCheck'
import { MdSearch, MdChat } from "react-icons/md";
import Avatar from './../helpers/avatar'
import ctrl from './../api_controller'


function ListItem(props){
    return (<div>
                <div className= "chatValue">{props.value}</div>
            </div>);
} 


export class Search extends React.Component{
    constructor(props){
        super(props)
        this.state={
            user: this.props.user,
            chat:false,
            search:'',
            conracts: null,
            searchFound:false,
            chatUser: null,
            dialogCheck:false,
            selectUser: null
        }  
                   
    }


    componentDidUpdate(prevProps){
        
    }
    

    componentDidMount() {
  
    }

    handleSearch = ({ target }) => {
        this.setState({ [target.name]: target.value, searchFound:false  },()=>this.searchChatUser())
    }
    
    search = ()=>{
        return(
            <div className='searchFrame'>
                <div className='searchDiv'>
                    <div className='searchInputDiv'>
                        <input className='searchInput' type='text' name='search' placeholder='Suche' value={this.state.search} onChange={this.handleSearch}></input>
                        <button className='searchIconBtn' onClick={()=>this.searchChatUser()}><MdSearch size={30}/></button>
                    </div>
                    {this.addChatUser()}
                </div>
            </div>
        )
    }

    searchChatUser = async()=>{
        if(this.state.search===''){
            return
        }
        const {search} = this.state
        const user = this.state.user.id
        const payload = {search, user}
        const users = await ctrl.getSearch(payload)
        if(users){
            this.setState({chatUser:users, searchFound:true })
        }
    }

    
    addChatUser = ()=>{
        if(this.state.searchFound){
            const chatUserArr = []
            const searchedList = this.state.chatUser
            
            searchedList.forEach(e=>{

                chatUserArr.push(
                    <div className='searchFoundFrame'>
                        <button className='searchUserBtn' onClick={()=>this.setState({dialogCheck:true,selectUser:e })}>
                            <div className='searchUserImgDiv'>
                                <Avatar name={e.userName} imgLow={e.imgLow} imgHigh={e.imgHigh}/>
                            </div>
                            <div className='searchUserNameDiv'>{e.userName}</div>
                            <div className='searchUserIcon'><MdChat size={36}/></div>
                        </button>
                        <DialogCheck 
                            text={<div>mchtest du <div style={{fontWeight: "bold"}}>{e.userName}</div> zu dein kontakten hinzufgen ?</div>}
                            open={this.state.dialogCheck}
                            checked={this.dialogCallback}
                        />

                    </div>
                )
            })
            
            const listItems = chatUserArr.map((items) =>
                <ListItem value={items} key={chatUserArr.indexOf(items) } />
                )
            return listItems;
        }else{
            return null
        }
    }

    dialogCallback = (e)=>{
        if(e){
            this.setState({search:'', dialogCheck:false, searchFound: false},()=>this.updateContactUser())
        }else{
            this.setState({search:'', dialogCheck:false, searchFound: false, selectUser: null})
        }
    }

    updateContactUser = ()=>{
        const id = this.state.user._id
        const chatUserId = this.state.selectUser.id
        if(id===chatUserId){
            return
        }
        const contacts = this.state.user.contacts
        if(contacts.find(e=> e===chatUserId)){
            return
        }
        const payload = {
            user: this.state.selectUser,
            chatUser: true

        }
        this.props.selectUser(payload)
        this.setState({selectUser: null})
        
        
    }

   




    render(){
        return(
            <div>
                {this.search()}
            </div>
        )  
    }
        
    
}

export default Search;



  