import axios from 'axios'
import url from './../../switch'



const configUni = {
  headers: {
    'Content-Type': 'application/json'
  },
}

// django
const serverUrl =  url.setUrl
const api = axios.create({
    baseURL: serverUrl+'/api',

})
export const insertUser = payload => api.post(`/user`, payload)
export const updateUserById = (id, payload) => api.patch(`/user/${id}`, payload)
export const deleteUserById = id => api.delete(`/user/${id}`)
export const getUserById = id => api.get(`/user/${id}`)
export const getUserbyEmailAndPassw = payload => api.put(`/user`, payload)
export const getUserbySearch = payload => api.patch(`/user`, payload)


// Filedata Img Django Rest-framework
///
const config = {
  headers: {
    'Content-Type': 'multipart/form-data;boundary=boundary',
    'Content-Disposition': 'attachment;filename=file.jpg',
  },
}
const apiPic = axios.create({
    baseURL: serverUrl+'/apiimg'
})
export const insertImg = payload => apiPic.post(`/images`, payload, config)
export const deleteImgById = id => apiPic.delete(`/images/${id}`)

const apiChatPic = axios.create({
  baseURL: serverUrl+'/apichatimg'
})
export const insertChatImg = payload => apiChatPic.post(`/images`, payload, config)
export const deleteChatImgById = id => apiChatPic.delete(`/images/${id}`)

// mobil api

const configMobil = {
  headers: {
    'Accept': 'application/json',
    'Content-Type': 'multipart/form-data'
  },
}
const apiPicMobil = axios.create({
  baseURL: serverUrl+'/apiimgmobil'
})
export const insertImgMobil = payload => apiPicMobil.post(`/images`, payload, configMobil)

const apiChatPicMobil = axios.create({
baseURL: serverUrl+'/apichatimgmobil'
})
export const insertChatImgMobil = payload => apiChatPicMobil.post(`/images`, payload)



const apiChat = axios.create({
    baseURL: serverUrl+'/apichat',
})
export const createChatroom = payload => apiChat.post(`/chatroom`, payload)
export const updateChatroom = (id,payload) => apiChat.put(`/chatroom/${id}`, payload)
export const deleteChatroomById = (id,payload) => apiChat.delete(`/chatroom/${id}`, payload)


const apiChats = axios.create({
    baseURL: serverUrl+'/apichats',
})
export const messReadHandleByChatroomId = (id,payload)=> apiChats.patch(`/chats/${id}`, payload)

const apiMess = axios.create({
  baseURL: serverUrl+'/apimess',
})
export const getNextMessByChatroomName = name => apiMess.get(`/${name}`)

const apiChatroomMap = axios.create({
    baseURL: serverUrl+'/apichatroommap',
})
export const getChatroomMapByUserId = id => apiChatroomMap.get(`/${id}`)
export const deleteChatroomMapByName = (id, payload) => apiChatroomMap.put(`/${id}`, payload)
  

const apis = {
    insertUser,
    updateUserById,
    deleteUserById,
    getUserById,
    insertImg,
    deleteImgById,
    createChatroom,
    updateChatroom,
    deleteChatroomById,
    getUserbyEmailAndPassw,
    getUserbySearch,
    getNextMessByChatroomName,
    getChatroomMapByUserId,
    messReadHandleByChatroomId,
    insertChatImg,
    deleteChatImgById,
    insertImgMobil,
    insertChatImgMobil,
    deleteChatroomMapByName
    
}


export default apis